                  <v-img width="100%" height="50px" contain :src="i.thumb"></v-img>
                </v-col>
                <v-col class="py-3" style="background-color:rgba(0,0,0,.6); border-bottom:1px solid rgba(255,255,255,.6);" cols="8">
