<script>
import bus from '@/utils/bus.js'
import axios from 'axios'
import TipTapView from '@/components/TipTap/TipTapView.vue'
export default {
  components:{
    TipTapView,
  },
  data(){
    return{
      dialog:false,
      carouselIndex:0,
      scrollReady:2,
      boards:null,
      output:{id:null,title:null,contents:null},
    }
  },
  computed:{
    listContainer(){
      switch(this.$vuetify.breakpoint.name){
        case 'xs' : return '100%';
        case 'sm' : return '100%';
        case 'md' : return '80%';
        case 'lg' : return '80%';
        case 'xl' : return '80%';
        default : return '100%';
      }
    },
    newsContainer(){
      switch(this.$vuetify.breakpoint.name){
        case 'xs' : return '0%';
        case 'sm' : return '0%';
        case 'md' : return '10%';
        case 'lg' : return '20%';
        case 'xl' : return '20%';
        default : return '0%';
      }
    },
  },
  created(){
    // this.preload();
    this.findBoards();
  },
  mounted(){
    
    this.$store.commit('set_showTipMessage',false);
    this.$nextTick(()=>{
      setTimeout( ()=>{bus.$emit('end:loading')},1000 ); // 로딩이 끝났다는걸 알려주기위한 마운트 된 후 비동기처리 셋타이머 ms인자 500때문에 기본딜레이 500잇음 0하면 페이지 마운트후 로딩) //
    });
  },
  methods:{
    clickTitle(i){
      this.output.id = this.boards[i]._id;
      this.output.title =  this.boards[i].title;
      this.output.contents = this.boards[i].contents;
      this.dialog=true;
    },
    preload(){
      for(var i = 0; i<this.preloadImg.length; i++)
      {
        var preloadImage = new Image();
        preloadImage.src = this.preloadImg[i]
      }
    },
    findThumb(){
      for(let i=0; i<this.boards.length; i++){
        let success = false;
        let c = this.boards[i].contents.content;
        for(let o=0; o<c.length; o++){
          if(c[o].content!=undefined&&success===false){
            let cc = c[o].content;
            for(let p=0; p<cc.length; p++){
              if(cc[p].type==='image'){
                this.boards[i].thumb = cc[p].attrs.src;
                success = true;
              }
            }
          }
        }
      }
    },
    findPretext(){
      for(let i=0; i<this.boards.length; i++){
        this.boards[i].pretext='';
        let success = false;
        let c = this.boards[i].contents.content;
        for(let o=0; o<c.length; o++){
          if(c[o].content!=undefined&&success===false){
            let cc = c[o].content;
            for(let p=0; p<cc.length; p++){
              if(cc[p].type==='text'){
                if(this.boards[i].pretext.trim().length < 200)this.boards[i].pretext = this.boards[i].pretext + ' ' + cc[p].text;
                if(this.boards[i].pretext.trim().length > 199 ){
                  success = true;
                  break;
                }
              }
            }
          }
        }
      }
    },
    findBoards(){
      axios.get('api/board/find')
      .then((res)=>{
        this.boards=res.data;
        this.findThumb();
        this.findPretext();
      })
    },

    moveCarousel(i){
      this.carouselIndex = i;
    },
    carouselTouchDown(){
      if(this.areaFocus) return;
      if(this.carouselIndex===0) return;
      if(this.carouselIndex<=2){
        this.$store.commit('set_showTipMessage',false);
        this.carouselIndex--;
      }
    },
    carouselTouchUp(){
      if(this.areaFocus) return;
      if(this.carouselIndex===2) return; 
      if(this.carouselIndex>=0){
        this.$store.commit('set_showTipMessage',false);
        this.carouselIndex++;
      } 
    },
    carouselScroll(e){
      if(this.scrollReady>0)
      {
        this.$store.commit('set_showTipMessage',false);
        if(e.wheelDelta>0){
          this.carouselIndex--;
          this.scrollReady=0;
        }
        else{
          if(this.carouselIndex!=2)
          {
          this.carouselIndex++;
          this.scrollReady=0;
          }
        }
      }
      else this.scrollReady++;
    },
  }
}
</script>



